<template>
    <br>
    <form>
        : 
        <input v-model="userName" type="text" >
        : <input v-model="message" type="text" @keyup="sendMessage">
        <div v-for="(item, idx) in recvList" :key="idx">
            <h3>: {{ item.userName }}</h3>
            <h3>: {{ item.content }}</h3>
        </div>
    </form>

    <form>
        <button type="button" @click="sendBtn">{{ btnContent }}</button>
        <div v-for="(item, idx) in btnClickList" :key="idx">
            <h3> : {{ item.clickContent }}</h3>
        </div>
    </form>
    <draggable>
    </draggable>
</template>

<script>
import Stomp from 'webstomp-client'
import SockJS from 'sockjs-client'
import { ref, defineComponent } from 'vue'

export default defineComponent ({
    name: 'App',
    setup() {


        //  
        const btnContent = ref(" ");
        const btnClickList = ref([]);
        const sendBtn = (e) => {
            e.preventDefault();
            sendClickContent()
        }

        const sendClickContent = () => {
            console.log("Send clickContent:" + btnContent.value);
            if (stompClient && stompClient.connected) {
                const msg = { 
                    clickContent: btnContent.value,
                };
                console.log(JSON.stringify(msg))
                stompClient.send("/clickReceive", JSON.stringify(msg), {});
            }
        }

        // 
        const userName = ref("");
        const message = ref("");
        const recvList = ref([]);

        const sendMessage = (e) => {
            if(e.keyCode === 13 && userName.value !== '' && message.value !== ''){
                send()
                message.value = ''
            }
        }
        const send = () => {
            console.log("Send message:" + message.value);
            if (stompClient && stompClient.connected) {
                const msg = { 
                    userName: userName.value,
                    content: message.value 
                };
                console.log(JSON.stringify(msg))
                stompClient.send("/receive", JSON.stringify(msg), {});
            }
        }

        //    
        let stompClient = null;
        const connect = () => {
            const serverURL = "http://192.168.0.75:8000/ws"
            let socket = new SockJS(serverURL);
            stompClient = Stomp.over(socket);
            console.log(`  .  : ${serverURL}`)
            stompClient.connect(
                {},
                frame => {
                //   
                    stompClient.connected = true;
                    console.log('  ', frame);
                    //    endpoint .
                    //  pub sub  .
                    stompClient.subscribe("/send", res => {
                        console.log('   .', res.body);

                        //   json   .
                        if(JSON.parse(res.body).clickContent) {
                            btnClickList.value.push(JSON.parse(res.body));
                            btnContent.value += "+"
                        } else if (JSON.parse(res.body).userName) {
                            recvList.value.push(JSON.parse(res.body))
                        }
                    });
                },
                error => {
                    //   
                    console.log('  ', error);
                    stompClient.connected = false;
                }
            );        
        }

        connect();

        return {
            btnContent,
            btnClickList,
            sendBtn,
            sendClickContent,
            
            // variable
            userName,
            message,
            recvList,
            
            // function
            sendMessage,
            send,
            
            connect
        }
    },
})
</script>