                    <h1 class="display-4"> </h1>
                </div>
                <div class="col-lg-7">
                    <div class="row">
                        <!--   -->
                        <div class="col-12">
                            <hr class="my-2">
                        </div>
                        <div class="col-10 px-4">
                            <div class="form-control" @click="searched = false">
                                <TagInput :allowCustom="true" tagBgColor="#f73e69" :customDelimiter="customDelimiter" v-model="tags" />
                            </div>
